html{
  background-color: #303030;
  box-sizing: border-box;
  font-size: 12px;
  font-family: '微軟正黑體',Arial;
  color: #fff;
}

body{
  height: 100vh;
  margin: 0;
}

a{text-decoration: none;}

.grid-container{
  display: grid;
  grid-template-areas: 
  "header"
  "main"
  "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 3rem;
}

header{
  grid-area: header;
  background-color: #000;
  box-shadow: 0 3px 5px #000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100vw;
  height: 5rem;
}

.hamburger{
  width: 50px;
  cursor: pointer;
  background: none;
  border: none;
  display: none;
}

.hamburger img{
  width: 100%;
}

.menu ul{
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 2rem;
}

.menu li{
  padding: 1.5rem;
  list-style: none;
}

.menu a{
  color: #fff;
  text-decoration: none;
}

.menu_item:hover{
  border-bottom: 2px solid #fff;
}

@media (max-width: 680px){

  .brand{
    display: flex;
    align-items: center;
    position: fixed;
    width: 100vw;
    height: 5rem;
    z-index: 100;
    background-color: #000;
  }
  
  .menu{
    width: 100%;
    background-color: #242424;
    transition: transform 0.5s;
    transform: translateY(-120px);
  }

  .menu ul{
    flex-direction: column;
    padding: 0;
  }

  .hamburger{
    display: flex;
  }
  
  header .open{
    transition: transform 0.5s;
    transform: translateY(170px);
  }
}

main{
  grid-area: main;
}

#title{
  width: 100%;
  height: 27vw;
  background-image: url('./mainpage_img/1546853775896.jpg') ;
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: 0px;
  box-shadow: 0 3px 5px #000;
}

#announce{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 1.5rem;
}

#announce h1{
  font-size: 2.5rem;
  color: rgb(255, 255, 255);
}

.announce_content{
  justify-content: center;
  font-size: 1.5rem;
}

.announce_list{
  display: flex;
  margin-bottom: 0.5rem;
}

.announce_list .brown{
  color: #e6a173;
  text-decoration: none;
}

.announce_list a{
  color: rgb(189, 189, 189);
  text-decoration: none;
  margin-left: 2.5rem;
 } 

 .announce_list a:hover{
  color: rgb(255, 255, 255);
 }

#introduce{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: auto;
  padding: 1.5rem;
  background-image: url('./mainpage_img/map.jpg');
  background-size: 100%;
  background-repeat: no-repeat;
  background-position-y: 60px;
  background-attachment: fixed;
}

#introduce h1{
  font-size: 2.5rem;
  color: rgb(255, 255, 255);
}

.intro_info{
  width: 50vw;
}

.intro_info p{
  font-size: 1.5rem;
}

.readmore{
  display: flex;
  align-content: center;
  justify-content: center;
}

.readmore h3{
  font-size: 1rem;
  padding: 1rem;
  text-decoration:none;
  color: #fac104;
  border-radius: 20px;
  border: #fac104 solid 2px;
}

.readmore h3:hover{
  background-color: #fac104;
  color: #000;
}

#control{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 1.5rem;
}

#control h1{
  font-size: 2.5rem;
  color: rgb(255, 255, 255);
}

.control_icons{
  width: 40vw;
  display: flex;
  justify-content: space-around;
  padding: 2rem;
  flex-wrap: wrap;
}

.control_icons img{
  margin: 1rem;
}

#download{
  background-image: url('./mainpage_img/game.jpg');
  background-size: 100vw;
  background-repeat: no-repeat;
  background-position-y: -150px;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 0 3rem;
}

#download h1{
  font-size: 2.5rem;
  color: rgb(255, 255, 255);
}

.download_btn{
  text-decoration: none;
  font-size: 1.5rem;
  padding: 1rem;
  color: #c6ffd7;
  border-radius: 20px;
  border: #c6ffd7 solid 3px;
}

.download_btn:hover{
  background-color: #c6ffd7;
  color: black;
}

#QA{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
}

#QA h1{
  font-size: 2.5rem;
  color: rgb(255, 255, 255);
}

.QA_div{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.QA_item{
  text-decoration: none;
  color:rgb(189, 189, 189);
  font-size: 1.5rem;
  margin: 0.7rem;
}

.QA_item:hover{
  color:rgb(255, 255, 255);
}

footer{
  grid-area: footer;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  background-color: #27020b;
}